.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.list-item {
    padding: 1em;
    margin: 2px;
    text-align: left;
}

.list-item:hover {
    background: #e8e8e8;
}

.list-item-loader {
    padding: 1em;
    background-image: url(./loader.gif);
    height: 10vh;
    background-repeat: no-repeat;
    background-position: center;
}

pre {
    color: white;
    background: #000000c9;
    font-size: small;
    margin: 0;
    overflow: auto;
}

h5 {
    margin: 1em;
    margin-left: 0;
}

.MuiFab-primary {
    background-color: black !important;
}

